import React from 'react'; import styled from 'styled-components'; 
// --------------
import MainText from './Components/HeaderInfo';


const Main = (props) => ( <MainWR>
                                <HeaderWR> 
        <div id='status'></div> 
        <div id="fb-root"></div>
        <div className="fb-login-button" style={{margin: '10px'}} data-size="large" data-button-type="continue_with" data-layout="default" data-auto-logout-link="false" data-use-continue-as="false" data-width=""></div>
        <MainText /> 
        <div className="fb-like" style={{margin: '10px'}} data-share="true" data-width="300" data-show-faces="true"></div>
                                </HeaderWR>
                          </MainWR>)

const MainWR = styled.div `display: flex; align-items: center; justify-content: center; 
box-shadow: inset 0 0 130px 2px #000; border-radius: 40px; 
padding: 20px; width: 100%; min-height: 93%; background: #fff; overflow: auto; font-family: 'Architects Daughter', cursive;`
// -------------------------------------------------------------------------------------------
const HeaderWR = styled.div `height: 100%; width: 100%;display: flex; flex-direction: column; justify-content: space-around; align-items: center;
}`
// -------------------------------------------------------------------------------------------
export default Main